@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}div,p,h1,h2,a,span,strong{font-family:"Courier Prime",monospace}a{display:inherit;font-weight:600;color:#0912e0}span,a,h1,h2,p{text-align:left}h1{font-size:24px}h2{font-size:18px}p{font-size:14px}a{font-size:14px}.App{display:grid}@media screen and (min-width: 859px){.App{grid-template-columns:repeat(2, 1fr)}}@media screen and (min-width: 1200px){.App{grid-template-columns:repeat(3, 1fr)}}.App div:first-child{grid-column:1/-1}.sides{padding:0 16px;width:calc(100% - 32px)}.bottom-l{margin-bottom:16px}.bottom{margin-bottom:8px}.bottom-s{margin-bottom:5.3333333333px}.grid{display:grid;grid-gap:4px;grid-template-columns:repeat(3, 1fr)}img{width:100%}.page-header{box-sizing:border-box;display:flex;height:55px;align-items:center;justify-content:space-between;font-size:22px}.page-header .logo{color:#0912e0}.collaborators .collaborator .who{background-color:#d9fc36}
#gradient-circles {
    position:absolute;
    width:100%;
    left:12.5%;
    top:25%;
}

text {
  position: absolute;
  top: 20%;
  left:50%;
  font-family: "Courier Prime", monospace;
  font-size: 75%;
  -webkit-animation: rotate 300s linear infinite;
          animation: rotate 300s linear infinite;
  transform-box: fill-box;
  -webkit-transform-origin: center;
          transform-origin: center;
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



